import React from 'react';
import './App.scss';

class App extends React.Component {
  state = {
    counter: 0,
  }

  addOne = () => {
    this.setState(prevState => ({
      counter: prevState.counter + 1,
    }));
  };

  add100 = () => {
    this.setState(prevState => ({
      counter: prevState.counter + 100,
    }));
  };

  increase = () => {
    if (this.state.counter % 5 === 0) {
      this.add100();
    }

    this.addOne();
  };

  render() {
    const { counter } = this.state;

    return (
      <div className="App">
        <h1>
          Count:
          {counter}
        </h1>
        <div className="buttons-wrapper">
          <button
            type="button"
            className="button"
            onClick={this.addOne}
          >
            Add one
          </button>
          <button
            type="button"
            className="button"
            onClick={this.add100}
          >
            Add 100
          </button>
          <button
            type="button"
            className="button"
            onClick={this.increase}
          >
            Increase
          </button>
        </div>
      </div>
    );
  }
}

export default App;
